.logo {
    width: 120px;
    /* background: rgba(255,255,255,.2);   */
    margin: 16px 28px 16px 0;
    float: left;
    line-height: 31px;
    text-align: center;
    color:#fff;
    font-weight: bold;
    font-family: '宋体';
  }
  .header {
    background: #fff !important;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.19) !important;
    position: relative !important;
    position: fixed !important;
    top: 0 !important;
    width: 100% !important;
    z-index: 100 !important;
  }
  .layoutContent{
      flex:1;
      padding-left: 200px;
      padding-top: 64px;
  }
  .layout{
    min-height:100%;
    
  }
  .logo img{
      width:100%; height:100%;
  }
  .logout{
    color:#333; 
    float:right;
    
  }
  .slide {
      position: fixed  !important;
      top: 64px  !important;
      bottom: 0  !important;
      overflow: auto  !important;
      overflow-x: hidden  !important;
      left: 0  !important;
  }